.gl_nav_wrap.cw_main {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: flex-start;
    position: absolute;
    width: 1400px;
    left: 50%;
    transform: translateX(-50%);
    top: auto;
    bottom: 0;
    z-index: 1;
}
.gl_nav {
    display: flex;
    vertical-align: top;
    width: 528px;
    background: #fff;
    height: 112px;
    padding-left: 52px;
    box-sizing: border-box;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
}
.gl_nav a{
    display: inline-block;
    position: relative;
    vertical-align: top;
    color: #d6583d;
    text-decoration: none;
    font-size: 24px;
    line-height: 1;
    transition: 0.3s;
}
.gl_nav a+a{
     margin-left: 36px;
}
.gl_nav a:after{
    display: block;
    position: absolute;
    content: '';
    height: 2px;
    width: 100%;
    left: 0;
    bottom: -1px;
    opacity: 1;
    background: #d6583d;
    transition: 0.3s;
}
.gl_nav a:hover:after{
    opacity: 0;
}
.gl_nav a:hover{
    color: #1f2225;
}
.gl_nav a.act{
    color: #1f2225;
}
.gl_nav a.act:after{
    opacity: 0;
}
.gl_nav_l, .gl_nav_r{
    display: inline-block;
    vertical-align: top;
    width: 135px;
    height: 112px;
    background: #fb5b39;
    cursor: pointer;
}
.gl_nav_l span, .gl_nav_r span{
    display: block;
    width: 68px;
    height: 112px;
    margin: 0 auto;
    opacity: 1;
    transition: 0.3s;
}
.gl_nav_l:hover span, .gl_nav_r:hover span{
    opacity: .4;
}
.gl_nav_l span{
    background: url(../images/slid_ar_prev.png)no-repeat center center;
    background-size: 100%;
}
.gl_nav_r span{
    background: url(../images/slid_ar_next.png)no-repeat center center;
    background-size: 100%;
}

.slick-initialized .slick-slide > div{
    display: block!important;
    font-size: 0;
    text-align: center;
}
